<template>
    <div class="mt-5">
        <div>
            <v-container v-for="(item, index) in tareas" :key="index">
                <v-hover>
                    <template v-slot="{ hover }">
                        <v-alert text dense :color="item.estado ? 'teal accent-4' : 'secondary'" class="my-0" :elevation="hover ? 24 : 3">
                            <v-chip
                            class="ma-2"
                            :color="item.estado ? 'teal accent-4' : 'secondary'"
                            text-color="white"
                            >
                                <v-avatar left>
                                    <v-icon small>assignment</v-icon>
                                </v-avatar>
                                {{item.titulo}}
                            </v-chip>
                            <div class="float-right mt-2" v-if="item.estado === true">
                                <v-btn color="green accent-3" fab x-small dark class="mx-1" @click="tareaRealizada(item._id)">
                                <v-icon>check</v-icon>
                                </v-btn>
                                <v-btn color="warning" fab x-small dark class="mx-1" @click="activarEdicion(item._id)">
                                <v-icon>edit</v-icon>
                                </v-btn>
                                <v-btn color="error" fab x-small dark class="mx-1" @click="eliminarTarea(item._id)">
                                <v-icon>delete</v-icon>
                                </v-btn>
                            </div>
                            <div class="float-right mt-2" v-if="item.estado === false">
                                <v-btn color="green accent-3" fab x-small dark class="mx-1" @click="tareaPorRealizar(item._id)">
                                <v-icon>undo</v-icon>
                                </v-btn>
                                <v-btn color="error" fab x-small dark class="mx-1" @click="eliminarTarea(item._id)">
                                <v-icon>delete</v-icon>
                                </v-btn>
                            </div>
                            <v-divider
                                class="my-4 info"
                                style="opacity: 0.22"
                            ></v-divider>
                            <div>{{item.descripcion}}</div>
                        </v-alert>
                    </template>
                </v-hover>
            </v-container>
            <v-hover>
                <template v-slot="{hover}">
                    <v-btn color="teal darken-1" dark fab bottom right fixed :elevation="hover ? 24 : 3" @click="dialog = true">
                        <v-icon>add</v-icon>
                    </v-btn>
                </template>
            </v-hover>
        </div>

        <v-dialog v-model="dialog" persistent max-width="90vw" width="500px">
            <v-card>
                <v-card-title class="text-center">
                    Nueva Tarea
                </v-card-title>
                <v-form @submit.prevent="agregarTarea()">
                    <v-card-text>
                            <v-text-field 
                            outlined
                            dense
                            color="teal" 
                            label="Titulo" 
                            name="titulo"
                            v-model="tarea.titulo"
                            ></v-text-field>
                            <v-textarea
                            outlined
                            name="descripcion"
                            label="Descripcion"
                            value=""
                            color="teal"
                            v-model="tarea.descripcion"
                            ></v-textarea>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn type="submit" color="success" fab small dark class="mx-1">
                        <v-icon>save</v-icon>
                        </v-btn>
                        <v-btn color="error" fab small dark class="mx-1" @click="dialog = false">
                        <v-icon>clear</v-icon>
                        </v-btn>
                    </v-card-actions>
                </v-form>
            </v-card>
        </v-dialog>

        <v-dialog v-model="dialogEditar" max-width="90vw" width="500px">
            <v-card>
                <v-card-title class="text-center">
                    Editar Tarea
                </v-card-title>
                <v-form @submit.prevent="editarTarea(tareaEditar)">
                    <v-card-text>
                            <v-text-field 
                            outlined
                            dense
                            color="teal" 
                            label="Titulo" 
                            name="titulo"
                            v-model="tareaEditar.titulo"
                            >{{tareaEditar.titulo}}</v-text-field>
                            <v-textarea
                            outlined
                            name="descripcion"
                            label="Descripcion"
                            value=""
                            color="teal"
                            v-model="tareaEditar.descripcion"
                            >{{tareaEditar.descripcion}}</v-textarea>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn type="submit" color="error" fab small dark class="mx-1">
                        <v-icon>save</v-icon>
                        </v-btn>
                        <v-btn color="success" fab small dark class="mx-1" @click="dialogEditar = false">
                        <v-icon>undo</v-icon>
                        </v-btn>
                    </v-card-actions>
                </v-form>
            </v-card>
        </v-dialog>


        <v-snackbar v-model="snackbar" multi-line>
        {{ texto }}
        <v-btn
            color="pink"
            text
            @click="snackbar = false"
        >
            Cerrar
        </v-btn>
        </v-snackbar>

        <v-dialog v-model="dialogDelete" persistent max-width="290">
            <v-card>
                <v-card-title class="headline"><v-icon color="error" class="mr-1">warning</v-icon>  Atencion</v-card-title>
                <v-card-text>De ser eliminada la tarea no se podran deshacer los cambios.</v-card-text>
                <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="green darken-1" text @click="dialogDelete = false">Deshacer</v-btn>
                <v-btn color="red darken-1" text @click="confirmar = true">Eliminar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

    </div>
</template>

<script>
export default {
    name: 'tareas',
    data() {
        return {
            tareas: [],
            tarea: {titulo: '', descripcion: ''},
            tareaEditar: {},
            tareaRealizar: {},
            tareaNoRealizada: {},
            dialog: false,
            dialogDelete: false,
            dialogEditar: false,
            confirmar: false,
            snackbar: false,
            texto: 'Snackbar',
        }
    },
    created() {
        this.listarTareas();
    },
    methods: {
        listarTareas(){
            this.axios.get('/tareas')
            .then((response) => {
                console.log(response.data);
                this.tareas = response.data;
            }).catch(err => {
                console.log('error' + err);
            });
        },
        agregarTarea(){
            this.axios.post('/agregar', this.tarea)
            .then(res => {
                console.log(res.data);
                this.tareas.push(res.data);
                this.tarea.titulo = '';
                this.tarea.descripcion = '';
                this.snackbar = true;
                this.texto = 'Tarea guardada';
                this.dialog = false;
            
            })
            .catch(err => {
                console.log(err.response);
                if (err.response.data.error.message) {
                    console.log(err.response.data.error.message);
                    this.tarea.titulo = '';
                    this.tarea.descripcion = '';
                    this.snackbar = true;
                    this.texto = 'Debe llenar todos los campos';
                }else{
                    this.snackbar = true;
                    this.texto = 'Error al guardar la tarea';
                    this.dialog = false;
                }
            });
        },
        eliminarTarea(id){
            console.log(id);
            // this.dialogDelete = true;
            this.axios.delete(`/tarea/${id}`)
            .then(res => {
                const index = this.tareas.findIndex(item => item._id === res.data._id);
                this.tareas.splice(index, 1);
                this.snackbar = true;
                this.texto = 'Tarea Eliminada';
                this.dialog = false;
            })
            .catch(err => {
                console.log(err.response);
                this.snackbar = true;
                this.texto = 'Error al eliminar la tarea';
                this.dialog = false;
            });
        },
        activarEdicion(id){
            console.log(id);
            this.dialogEditar = true;
            this.axios.get(`/tarea/${id}`)
            .then(res => {
                this.tareaEditar = res.data;
            })
            .catch(err => {
                console.log(err.response);
            })
        },
        editarTarea(item){
            console.log(item);
            this.axios.put(`/tarea/${item._id}`, item)
            .then(res => {
                const index = this.tareas.findIndex(i => i._id === res.data._id);
                this.tareas[index].titulo = res.data.titulo;
                this.tareas[index].descripcion = res.data.descripcion;
                this.snackbar = true;
                this.texto = 'Se guardaron los cambios';
                this.dialogEditar = false;
            })
            .catch(err => {
                console.log(err.response);
                this.snackbar = true;
                this.texto = 'Error! No se guardaron los cambios';
                this.dialogEditar = false;
            });
        },
        tareaRealizada(id){
            console.log(id);
            this.axios.get(`/tarea/${id}`)
            .then(res => {
                console.log(res.data);
                this.tareaRealizar = res.data;
                this.realizarTarea(this.tareaRealizar);
            })
            .catch(err => {
                console.log(err.response);
            });
        },
        realizarTarea(item){
            this.axios.put(`/tareaRealizada/${item._id}`, item)
            .then(res => {
                const index = this.tareas.findIndex(n => n._id === res.data._id);
                this.tareas[index].estado = res.data.estado;
                this.snackbar = true;
                this.texto = 'Tarea realizada';
                this.dialogEditar = false;
            })
            .catch(err => {
                console.log(err.response);
                this.snackbar = true;
                this.texto = 'Error! No se modifico la tarea';
                this.dialogEditar = false;
            });
        },
        tareaPorRealizar(id){
            console.log(id);
            this.axios.get(`/tarea/${id}`)
            .then(res => {
                console.log(res.data);
                this.tareaNoRealizada = res.data;
                this.porRealizarTarea(this.tareaNoRealizada);
            })
            .catch(err => {
                console.log(err.response);
            });
        },
        porRealizarTarea(item){
            this.axios.put(`/tareaPorRealizar/${item._id}`, item)
            .then(res => {
                const index = this.tareas.findIndex(n => n._id === res.data._id);
                this.tareas[index].estado = res.data.estado;
                this.snackbar = true;
                this.texto = 'Tarea no realizada';
                this.dialogEditar = false;
            })
            .catch(err => {
                console.log(err.response);
                this.snackbar = true;
                this.texto = 'Error! No se modifico la tarea';
                this.dialogEditar = false;
            });
        }
    }
}
</script>